@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600);
*{box-sizing: border-box;}

html, body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
	font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


ul, li{list-style: none;margin: 0;padding: 0;}

p, h1, h2, h3 ,h4 ,h5, h6{margin: 0;padding: 0;}

a{text-decoration: none;color: #000;color: initial;}
a:hover{color: #00AB44;}

.container{width: 100%;max-width: 1400px;padding-left: 15px;padding-right: 15px;padding-top: 50px;}
.HeaderWrap ul{display:flex;justify-content:space-between}.HeaderWrap{padding:15px}.active{font-weight:700}.active a{color:#00ab44}
.aboutSection{padding-left:15px;margin-top:50px}.aboutSection li{margin:10px 0;list-style-type:disc}.NameWrap h3{margin:0 0 10px;color:#00ab44}.NameWrap a{font-size:14px}.aboutSection h2{text-transform:uppercase;color:#00ab44}
.clientWrap{display:flex;flex-wrap:wrap}.clientWrap p{margin-bottom:30px;flex-basis:calc(25% - 20px);max-width:calc(25% - 20px);margin-left:10px;margin-right:10px;font-weight:500}
.ExperienceWrap{display:flex;flex-direction:column}.companyDetails{margin-bottom:30px}.companyName{display:flex;align-items:center}.companyDetails a{margin:0 0 10px;display:block}.ExperienceWrap h2{margin-bottom:50px;color:#00ab44}
.OthersWrap h2{margin-bottom:10px;text-transform:uppercase;color:#00ab44}.OthersWrap .certificationsWrap{margin-top:50px;margin-bottom:50px}.OthersWrap .coCurrWrap ul{padding-left:15px}.OthersWrap .coCurrWrap li{list-style-type:disc;margin-bottom:5px}
.projectSubWrap{display:flex;justify-content:space-between;flex-wrap:wrap}.prjctWrap{margin-bottom:40px;flex-basis:calc(33.33% - 20px);max-width:calc(33.33% - 20px);margin-left:10px;margin-right:10px}.projectSubWrap a{margin:10px 0 0;display:block;font-weight:500}.ProjectsWrap h2{margin-bottom:50px;text-transform:uppercase;color:#00ab44}.prjctWrap h3{color:#00ab44}.prjctWrap h3 span{color:#000;font-size:16px}
